@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");

.notfound_page {
    width: 100%;
    min-height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    font-family: "Ubuntu", sans-serif;

}

.notfound_page h1 {
